import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
import uniIcon from "@/components/uni-icons/uni-icons.vue";
import topTabbar from "@/components/topTabbar-follow.vue";
let _this,timer;
export default {
	components:{
		uniLoadMore,
		uniIcon,
		topTabbar
	},
	data() {
		return {
			pupDefault:'',
			baseUrl:'/static/images/jinsy/alert/',
			items: ['我的跟进','待跟进'],
			loadingType: 'more',
		
			followList:[],
			keyword:"",
			scope: 0,
			pageNum: 1,
			pageSize: 20,
			isDoRefresh:false,
			isShowButton:false
		};
	},
	onReachBottom:function(){
		 if (timer != null) {
		            clearTimeout(timer);
		        }
		   timer = setTimeout(function() {
		        _this.fetchMoreList();
		        }, 1000);
	},
	onPullDownRefresh:function(){
		
		this.fetchList();
	},
	onLoad:function(){
		_this = this
	    this.pupDefault = uni.getStorageSync('pupDefault')
	    if (this.pupDefault == "BUY_DEPUTY"){
	    	this.isShowButton = true
	    }
		this.fetchList()
		
	},
	onShow:function(){
		   let pages = getCurrentPages();
		   let currPage = pages[pages.length-1];
		   if (currPage.data.isDoRefresh == true){
		   	       currPage.data.isDoRefresh = false;
		   		   this.fetchList()
		   	}
			

	},
	methods:{
		async fetchList(){
			this.pageNum =1
			let data={
				// postCode: this.pupDefault,  //身份编码
				keyword:this.keyword,  //	搜索关键字
				scope:this.scope,			//我的跟进:1，订单跟进:2
				pageNum:  this.pageNum,
				pageSize: this.pageSize,
			}
			const res = await this.$http.get('/portrayal/deputy_list',{data:data})
			console.log(res)
			this.pageNum += 1
			
			this.followList = res.data.data.list
			uni.stopPullDownRefresh(); //得到数据后停止下拉刷新
		},
		async fetchMoreList(){
			if (_this.loadingType !== 'more') {//loadingType!=0;直接返回
				return false;
			}
			_this.loadingType = 'loading';
			uni.showNavigationBarLoading();//显示加载动画
			let data={
				// postCode: this.pupDefault,  //身份编码
				keyword:this.keyword,  //	搜索关键字
				scope:this.scope,
				pageNum:  this.pageNum,
				pageSize: this.pageSize,
			}
			const res = await this.$http.get('/portrayal/deputy_list',{data:data})
			if (res.data.data.list.length==0){
				_this.loadingType = '';
				uni.hideNavigationBarLoading();//关闭加载动画
				return;
			}
			this.pageNum += 1
			this.followList = this.followList.concat(res.data.data.list)
			_this.loadingType = 'more';//将loadingType归0重置
			uni.hideNavigationBarLoading();//关闭加载动画
			
		},
		toDetail:function(e){
			uni.navigateTo({
				url: `./detailFollowRecord?recordCode=${e}`,
				success: res => {},
				fail: (err) => {console.log(err)},
				complete: () => {}
			});
			
		},
		tabSwitch:function(index){
			this.activeIndex = index
			console.log('activeIndex',this.activeIndex)
			if (this.activeIndex ==0){
				 this.scope = 0
				}
			if (this.activeIndex ==1){
				this.scope = 1
			}
			this.fetchList()
		},
		tapSearch:function() {
			console.log(this.keyword)
			this.fetchList()
		
		},
		addFollow:function(){
			uni.navigateTo({
				url: './addFollowRecord',
				success: res => {},
				fail: (err) => { console.log(err)},
				complete: () => {}
			});
		}
		
	}
}
